.cart-header {
    padding-top: 0.5rem;
  }
  
  .orders {
    margin: 5rem 0 -6.5rem 0;
    min-height: calc(100vh - 15rem);
  }
  
  .orders-heading,
  .orders-total {
    width: 100%;
    max-width: 600px;
  }
  .orders-heading {
    margin-bottom: 5.5rem;
    border-bottom: 2px solid #000;
  }
  .orders-total {
    text-align: right;
    border-top: 2px solid #000;
    margin-left: auto;
    margin-top: 3rem;
  }